import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { ProductsService } from '@jmart-cabinet/modules/products/services/products.service';
import { FormControl } from '@angular/forms';
import { Observable, Subject } from 'rxjs';
import { debounceTime, distinctUntilChanged, map, merge, pluck, scan, share, startWith, switchMap, tap } from 'rxjs/operators';
import { ProductsItem } from '@jmart-cabinet/modules/products/models/products.model';

@Component({
  selector: 'j-binding-modal',
  templateUrl: './binding-modal.component.html',
  styleUrls: ['./binding-modal.component.scss']
})
export class BindingModalComponent implements OnInit {
  @Input() productItem;
  @Input() type;
  @Output() refresh = new EventEmitter();
  loading = false;
  loadingItems = false;
  selectProduct = '';

  terms = '';
  control: FormControl;
  private searchStream = new Subject<string>();
  productItems$: Observable<ProductsItem[]>;
  readonly theme = { height: '3rem', width: '100%', 'background-color':  '#ECEEF1' };
  constructor(public modal: NgbActiveModal, private productsService: ProductsService) {
  }

  ngOnInit(): void {
    if (this.type === 2){
      this.terms = this.productItem.model;
      this.control = new FormControl(this.terms);
    } else {
      this.control = new FormControl(this.terms);
    }
    const searchSource = this.searchStream.pipe(
      tap(() => this.loadingItems = true),
      debounceTime(500),
      distinctUntilChanged(),
      map(searchTerm => {
        return {terms: searchTerm};
      }),
    );
    const source = searchSource.pipe(
      startWith({ terms: this.terms }),
      switchMap((params: {terms: string, page: number}) => {
        return this.productsService.getAllProducts(params.terms);
      }),
      share(),
      tap(() => this.loadingItems = false),
    );
    this.productItems$ =  source.pipe(
      pluck('products'),
    );
  }
  public search(term: string): void {
    this.terms = term;
    this.searchStream.next(term);
  }
  clarify(){
    this.loading = true;
    this.productsService.clarifyProduct(this.productItem.id, this.selectProduct).subscribe(
      data => {
        this.modal.dismiss();
        this.refresh.emit();
      },
      error => {
        this.loading = false;
        alert('Ошибка! Обратитесь в службу поддержки Jusan Магазин ');
      }
    );
  }
  onImgError(event){
    event.target.src = './assets/jmart/empty-img.svg';
  }
}
