import { Inject, Injectable, Injector } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { ImportLastInfo, IProductsResult, ProductsCard, ProductsItem, ProductsPagination } from '../models/products.model';

@Injectable({
  providedIn: 'root'
})
export class ProductsService {

  constructor(
    @Inject('JMART_CABINET_BASE_URL') private baseUrl: string,
    private http: HttpClient,
  ) {}

  getProducts(search: string = '', page: number = 1): Observable <IProductsResult<ProductsItem, ProductsPagination>>  {
    const url = `${this.baseUrl}/gw/catalog/v1/my/products`;
    const params =  new HttpParams()
      .set('search', search)
      .set('page', String(page));
    return this.http.get<any>(url, { params }).pipe(map(res => res.data));
  }

  getProduct(id): Observable <ProductsCard>  {
    const url = `${this.baseUrl}/gw/catalog/v1/my/products/${id}`;
    return this.http.get<any>(url).pipe(map(res => res.data));
  }

  importProduct(body): Observable <any>  {
    const url = `${this.baseUrl}/gw/catalog/v1/my/vendor-product-import/import`;
    return this.http.post<any>(url, body).pipe(map(res => res.data));
  }
  getLastImportFile(): Observable <ImportLastInfo>  {
    const url = `${this.baseUrl}/gw/catalog/v1/my/vendor-product-import-file/last`;
    return this.http.get<any>(url).pipe(map(res => res.data));
  }
  getProductsImport(model: string = '', page: number = 1, status= 1): Observable <IProductsResult<ProductsItem, ProductsPagination>>  {
    const url = `${this.baseUrl}/gw/catalog/v1/my/vendor-product-import`;
    const params =  new HttpParams()
      .set('search', model)
      .set('page', String(page))
      .set('status', String(status));
    return this.http.get<any>(url, { params }).pipe(map(res => res.data));
  }
  getImportProductStatusCount(): Observable <any>  {
    const url = `${this.baseUrl}/gw/catalog/v1/my/vendor-product-import/process-status`;
    return this.http.get<any>(url).pipe(map(res => res.data));
  }
  getAllProducts(query: string): Observable <any>  {
    const url = `${this.baseUrl}/gw/catalog/v1/products?page=1&query=${query}&show_totals=true`;
    return this.http.get<any>(url).pipe(map(res => res.data));
  }
  clarifyProduct(id, productCode): Observable <any> {
    const url = `${this.baseUrl}/gw/catalog/v1/my/vendor-product-import/clarify/${id}`;
    return this.http.put<any>(url, {'product_code': productCode}).pipe(map(res => res.data));
  }
  changeProductStatus(id, status): Observable <any>{
    const url = `${this.baseUrl}/gw/catalog/v1/my/products/status-change/${id}`;
    return this.http.put<any>(url, {'status': status}).pipe(map(res => res.data));
  }
  changeProductStoreStatus(id, storeId, status): Observable <any>{
    const url = `${this.baseUrl}/gw/catalog/v1/my/company-store-products/change-availability/${storeId}/${id}`;
    return this.http.put<any>(url, {'available_status': status}).pipe(map(res => res.data));
  }
  changeProductBasePrice(id, price): Observable <any>{
    const url = `${this.baseUrl}/gw/catalog/v1/my/product-prices/change/${id}`;
    const headers =  new HttpHeaders()
      .set('Content-Type', 'application/x-www-form-urlencoded; charset=UTF-8');
    const params = new HttpParams({
      fromObject: { price: price},
    });
    return this.http.put<any>(url, params.toString(), {headers}).pipe(map(res => res.data));
  }
  changeProductCityPrice(id, cityId, price): Observable <any>{
    const url = `${this.baseUrl}/gw/catalog/v1/my/product-prices/change-by-city/${id}`;
    const headers =  new HttpHeaders()
      .set('Content-Type', 'application/x-www-form-urlencoded; charset=UTF-8');
    const params = new HttpParams({
      fromObject: { price: price, city_id: cityId },
    });
    return this.http.put<any>(url, params.toString(), {headers}).pipe(map(res => res.data));
  }
  deleteProductCityPrice(id, cityId): Observable <any>{
    const url = `${this.baseUrl}/gw/catalog/v1/my/product-prices/delete-by-city/${id}/${cityId}`;
    return this.http.delete<any>(url).pipe(map(res => res.data));
  }
  getCities() {
    const url = `${this.baseUrl}/gw/dictionary/v1/cities`;
    return this.http.get<any>(url)
      .pipe(map(res => res.data));
  }
}
