import { Component, ElementRef, HostListener, Input, OnInit, ViewChild } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { debounceTime, distinctUntilChanged, pluck, map, startWith, share, tap, switchMap, scan, merge } from 'rxjs/operators';
import { ActivatedRoute, Router } from '@angular/router';
import { FormControl } from '@angular/forms';
import { FILTER_COMISSION } from '@filter/filter.model';
import { ProductsService } from '../../services/products.service';
import { ProductsItem, ProductsPagination } from '../../models/products.model';

@Component({
  selector: 'j-main-product-page',
  templateUrl: './main-product-page.component.html',
  styleUrls: ['./main-product-page.component.scss'],
  // providers: [ProductsService]
})
export class MainProductPageComponent implements OnInit {
  @ViewChild('myDiv') myDivRef: ElementRef;
  productItems$: Observable<ProductsItem[]>;
  productsPagination: ProductsPagination;

  terms = '';
  control: FormControl;
  private searchStream = new Subject<string>();
  page = 1;
  newSearch = false;
  loading = true;
  private pageStream = new Subject<number>();
  readonly theme = { height: '7rem', 'background-color': '#ECEEF1' };
  @HostListener('window:scroll', ['$event']) // for window scroll events
  onScroll(event) {
    const pos = (document.documentElement.scrollTop || document.body.scrollTop) + document.documentElement.offsetHeight;
    const max = document.documentElement.scrollHeight;
    if (pos === max)   {
      this.goToNextPage();
      console.log(true);
    }
  };
  constructor(private productsService: ProductsService,
              private router: Router,
              private route: ActivatedRoute) {
    this.terms =  this.route.snapshot.queryParams?.terms || '';
    this.control = new FormControl(this.terms);
  }

  ngOnInit(): void {
    const searchSource = this.searchStream.pipe(
      debounceTime(500),
      distinctUntilChanged(),
      map(searchTerm => {
        this.terms = searchTerm;
        this.router.navigate([], {
          queryParams: {
            terms: this.terms
          }
        });
        return {terms: searchTerm, page: 1};
      }),
      tap(() => { this.newSearch = true; this.myDivRef.nativeElement.scrollTop = 0; })
    );
    const pageSource = this.pageStream.pipe(
      tap(() => this.newSearch = false),
      map(pageNumber => {
        this.page = pageNumber;
        return {terms: this.terms, page: pageNumber};
      })
    );
    const source = pageSource.pipe(
      merge(searchSource), // новая весия mergeWith
      tap(() => this.loading = true),
      startWith({terms: this.terms, page: this.page}),
      switchMap((params: {terms: string, page: number}) => {
        return this.productsService.getProducts(params.terms, params.page);
      }),
      share(),
    );
    this.productItems$ =  source.pipe(
      pluck('items'),
      scan((acc, data) => this.newSearch ?  data : [...acc, ...data], []),
      tap(() => this.loading = false),
    );
    source.pipe(pluck('pagination')).subscribe(data => this.productsPagination = data);
  }
  public search(term: string): void {
    this.searchStream.next(term);
  }
  public addNextProductItems(page: number) {
    this.pageStream.next(page);
  }
  public goToNextPage() {
    if (this.productsPagination.lastPage > this.productsPagination.currentPage) {
      this.addNextProductItems( this.productsPagination.currentPage + 1);
    }
  }

  routeClick(productId: string) {
    this.router.navigate(['jmart-cabinet', 'product-card', productId], { queryParamsHandling: 'preserve' });
  }

}
