import { Component, OnInit } from '@angular/core';
import { ImportLastInfo } from '../../models/products.model';
import { ProductsService } from '../../services/products.service';

@Component({
  selector: 'j-import-product-page',
  templateUrl: './import-product-page.component.html',
  styleUrls: ['./import-product-page.component.scss']
})
export class ImportProductPageComponent implements OnInit {
  tabId = 0;
  public importLastInfo: ImportLastInfo;
  importState = {
    loading: false,
    errorText: '',
    fileName: '',
    uploadStatus: ''
  };
  constructor(private productsService: ProductsService) { }

  ngOnInit(): void {
    this.getLastImportFile();
  }
  controlChange(event) {
    this.tabId = event;
    this.importState.fileName = '';
  }
  uploadFile(file: File) {
    this.importState.fileName = file?.name;
    const formData: FormData = new FormData();
    formData.append('file', file);
    this.uploadFileRequest(formData);
  }
  uploadLink(file: string) {
    this.importState.fileName = file;
    const formData: FormData = new FormData();
    formData.append('file', file);
    this.uploadFileRequest(formData);
  }
  uploadFileRequest(data: any) {
    this.importState.loading = true;
    this.importState.errorText = '';
    this.productsService.importProduct(data).subscribe(
      () => {
        this.importState.loading = false;
        this.importState.fileName = '';
        this.getLastImportFile();
      },
      error => {
        this.importState.loading = false;
        this.importState.errorText = error?.error?.data?.message || error.statusText;
        this.importState.fileName = '';
        this.getLastImportFile();
        console.log(error);
      });
  }
  getLastImportFile() {
    this.productsService.getLastImportFile().subscribe(data => {
      this.importLastInfo = data;
    });
  }

  onUploadingFilesUpdate(event) {
  }
}
