import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'j-upload-products',
  templateUrl: './upload-products.component.html',
  styleUrls: ['./upload-products.component.scss']
})
export class UploadProductsComponent {
  @Input() importState ;
  @Output() uploadFile: EventEmitter<File> = new EventEmitter();
  @Input() fileStatus;
  @Input() fileStatusMessage;
  handle(file: File): void {
    this.uploadFile.emit(file);
  }
  openFile() {
    document.getElementById('openFile').click();
  }
}
