import { Component, HostBinding, Input, OnInit } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import {  trigger, style, transition, animate} from '@angular/animations';

@Component({
  selector: 'j-carousel-products',
  templateUrl: './carousel-products.component.html',
  styleUrls: ['./carousel-products.component.scss'],
  animations: [trigger('slideUp', [
    transition('* <=> *', [
      style({transform: 'translateY(-{{startHeight}}%)', overflow: 'hidden'}),
      animate('13s ease'),
    ], {params: {startHeight: '0'}})
  ])]
})
export class CarouselProductsComponent implements OnInit {
  @Input() links: any = [];
  public currentIndex = 0;
  @HostBinding('style.--slide')
  private slide = 0;
  down() {
    if (this.links?.length > this.currentIndex + 1 ) {
      if (this.currentIndex < (this.links?.length - 5)) {
        --this.slide;
      }
      this.currentIndex++;
    }
  }
   up() {
    if (0 < this.currentIndex) {
      if (this.currentIndex + 1 > 5) {
        ++this.slide;
      }
      this.currentIndex--;
    }

  }

  constructor(private modalService: NgbModal) { }

  ngOnInit(): void {

  }
  open(content) {
    const modalRef =   this.modalService.open(content, {
      backdropClass: 'backdrop_light',
      windowClass: 'modal_light modal_dictionary modal-adaptive modal-holder',
      centered: true,
    });
    modalRef.componentInstance.currentIndex = this.links[this.currentIndex];
  }
  onImgError(event){
    event.target.src = './assets/jmart/empty-img.svg';
  }

}
