import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'j-import-link-upload',
  templateUrl: './import-link-upload.component.html',
  styleUrls: ['./import-link-upload.component.scss']
})
export class ImportLinkUploadComponent implements OnInit {
  @Output() uploadLink: EventEmitter<string> = new EventEmitter<string>();
  @Input() importState;
  link: string;
   constructor() { }

  ngOnInit(): void {
  }
  getLink(link) {
     this.link = link;

  }
  updateLink() {
     this.uploadLink.emit(this.link);
  }
}
