import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { JmartCabinetLayoutComponent } from '@jmart-cabinet/components/jmart-cabinet-layout/jmart-cabinet-layout.component';
import { JmartCabinetNoLayoutComponent } from '@jmart-cabinet/components/jmart-cabinet-no-layout/jmart-cabinet-no-layout.component';
import { BindingProductPageComponent } from './pages/binding-product-page/binding-product-page.component';
import { ImportProductPageComponent } from './pages/import-product-page/import-product-page.component';
import { MainProductPageComponent } from './pages/main-product-page/main-product-page.component';

import { ProductsComponent } from './products.component';
const routes: Routes = [
  {
    path: '',
    component: JmartCabinetLayoutComponent,
    children: [
      {
        path: '',
        component: ProductsComponent,
        data: {
          title: 'SETTINGS.TITLE',
        },
        children: [
          {
            path: 'main',
            component: MainProductPageComponent,
          },
          {
            path: 'import',
            component: ImportProductPageComponent,
          },
          {
            path: 'binding',
            component: BindingProductPageComponent,
          },
          {
            path: '',
            redirectTo: 'main',
          },
          {
            path: '**',
            redirectTo: 'main',
          },
        ],
      },
    ],
  },
  {
    path: 'no-layout',
    component: JmartCabinetNoLayoutComponent,
    children: [
      // put product card here
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ProductsRoutingModule {}
