import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { ProductsService } from '@jmart-cabinet/modules/products/services/products.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { BindingModalComponent } from '@jmart-cabinet/modules/products/pages/binding-product-page/binding-modal/binding-modal.component';

@Component({
  selector: 'j-binding-item',
  templateUrl: './binding-item.component.html',
  styleUrls: ['./binding-item.component.scss']
})
export class BindingItemComponent implements OnInit {
  @Input() productItem: any;
  @Input() type;
  @Output() refresh = new EventEmitter();
  selectProduct = '';
  loading = false;

  constructor(private modalService: NgbModal,
              private productsService: ProductsService) { }

  ngOnInit(): void {
  }

  open() {
    this.selectProduct = '';
    const modalRef =   this.modalService.open(BindingModalComponent, {
      backdropClass: 'backdrop_light',
      windowClass: 'modal_light modal_md modal-holder',
      centered: true,
    });
    modalRef.componentInstance.productItem = this.productItem;
    modalRef.componentInstance.type = this.type;
    modalRef.componentInstance.refresh.subscribe(() => {
      this.productItem.clarify = true;
      this.refresh.emit(this.type);
    } );
  }
  clarify(product){
    console.log(product);
    this.loading = true;
    this.productsService.clarifyProduct(product.id, product.productVariants[0].productCode).subscribe(
      data => {
        console.log(data);
        this.loading = false;
        product.clarify = true;
        this.refresh.emit(this.type);
      },
      error => {
        console.log(error);
        alert('Ошибка! Обратитесь в службу поддержки Jusan Магазин ');
      }
    );
  }
  onImgError(event){
    event.target.src = './assets/jmart/empty-img.svg';
  }

}
