import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControl } from '@angular/forms';

export interface DeliveryToggleMode {
  id: number;
  label: string;
}

export type DeliveryToggleModesTuple = [
  DeliveryToggleMode,
  DeliveryToggleMode,
];

@Component({
  selector: 'j-import-mode-toggle',
  templateUrl: './import-mode-toggle.component.html',
  styleUrls: ['./import-mode-toggle.component.scss']
})
export class ImportModeToggleComponent implements OnInit {

  @Output() controlChange = new EventEmitter();
  currentId = 0;
  constructor() {}

  mods: DeliveryToggleModesTuple = [
    {
      id: 0,
      label: 'Загрузка файла',
    },
    {
      id: 1,
      label: 'Автообновление',
    },
  ];

  ngOnInit(): void {
  }

  setMod(mode: DeliveryToggleMode) {
    this.currentId = mode.id;
    this.controlChange.emit(mode.id);
  }

}
